 * @LastEditTime: 2019-08-20 15:23:56
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="reset_wrapper">
    <Header></Header>

    <el-row type="flex" justify="center" align="middle" class="reset_content">
      <el-col :xs="4" :sm="6" :md="8" :lg="9" :xl="2" class="reset_page box">
        <div class="title">重置密码</div>
        <div class="deputy_title">重置登录密码后，24小时内禁止提币</div>
        <el-form >
          <el-form-item prop="emailAccount">
            <el-input v-model="emailAccount" placeholder="邮箱账号" autofocus="autofocus"  @keyup.enter.native="resetS"></el-input>
          </el-form-item>
          
          <el-form-item class="reset_btn">
            <el-button type="primary" @click.native="resetS" :loading="btnLoad">提交</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

    <user-foot></user-foot>

  </div>

</template>

<script>
  import Header from '@/components/header/header'
  import UserFoot from '@/components/userFoot/userFoot'
  import { vaildEmail} from '@/utils/validate'

  export default {
   data() {
     return {
      emailAccount:'',
      btnLoad:false,
    }
   },
   methods: {
    /* 验证邮箱 */
    resetS(){
      debugger
      if(!this.btnLoad){
        if(!vaildEmail(this.emailAccount)){
          this.$message.info('请输入正确格式的邮箱')
          return false
        }
        const data = {"email":this.emailAccount}
        this.btnLoad = true
        this.$store.dispatch('valid_email',data).then(res => {
          if(res.data.result == 0){
            this.btnLoad = false
            this.$router.replace({path:'/verify',query:{email:this.emailAccount}})
          }else{
            this.btnLoad = false
          }
        }).catch(err => {
          this.btnLoad = false
        })
      }
    },
    /**
     * 跳转页面
     */
    goTo(str){
      this.$router.replace('/'+str)
    }
   },
   components: {
     Header,UserFoot
   }
  }
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/style/base.scss';

  .reset_wrapper{
    height: 100%;
    width: 100%;
    min-width: 1280px;
    padding-top: 47px;
    .reset_content{
      width: 100%;
      height: calc(100% - 180px);
      background-color:$module_bg_color;
      position: relative;
      .reset_page{
        width: 80%;
        max-width: 480px;
        min-width: 400px;
        max-height: 320px;
        border-radius: 5px;
        background-color: $light_font_color;
        padding:30px 50px;
        box-sizing: border-box;
        position: absolute;
        top:50%;
        left: 50%;
        margin-top: -180px;
        margin-left: -190px;
      }
      .title{
        width: 100%;
        height: 40px;
        line-height: 40px;
        font-size: $font20;
        letter-spacing: 2px;
        color: $light_title_color;
        text-align: center;
        margin-bottom: 20px;
      }
      .deputy_title{
        width: 100%;
        height: 20px;
        line-height: 20px;
        font-size: $base_font;
        letter-spacing: 2px;
        color: $warn_font_color;
        text-align: center;
        margin-bottom: 20px;
      }
      .reset_btn{
        margin-bottom: 10px;
        .el-button{
          width: 100%;
          background-color: $user_btn_bg_btn;
          border-radius: 3px;
          font-size: $font14;
          letter-spacing: 1px;
          margin-top: 10px;
        }
      }
    }
  }
</style>



